import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket-category-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class TicketCategoryAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
ticketCategoryForm: FormGroup;
static SerialnoMandatory ="0";
ticketcatId = 0;  
ajaxData : TicketCategoryData[];
ticketcategory = new TicketCategoryData(); 
config = new configDetails(); 
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.ticketcatId).subscribe(params=>{
			if(params.ticketcatId != undefined){
				this.ticketcatId=params.ticketcatId; 
			}
		}); 
	console.log("ticketcatId=="+this.ticketcatId);
	if(this.ticketcatId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.ticketCategoryForm = new FormGroup({
			'ticketcatId' : new FormControl(''),
			'ticketcatName' : new FormControl('', [ Validators.required]),
			'ticketcatEntryId' : new FormControl(''),
			'ticketcatEntryDate' : new FormControl(''),
			'ticketcatModifiedId' : new FormControl(''),
			'ticketcatModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get ticketcatName(){ return this.ticketCategoryForm.get('ticketcatName')};
	    get ticketcatEntryId(){ return this.ticketCategoryForm.get('ticketcatEntryId')};
	    get ticketcatEntryDate(){ return this.ticketCategoryForm.get('ticketcatEntryDate')};
	    get ticketcatModifiedId(){ return this.ticketCategoryForm.get('ticketcatModifiedId')};
	    get ticketcatModifiedDate(){ return this.ticketCategoryForm.get('ticketcatModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<TicketCategoryData>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/helpdeskcategory-data ',
							 			JSON.stringify({ 'ticketcatId' :this.ticketcatId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.ticketcategory = data['populateData'][0];
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['helpdesk/ticket-category-list']); 
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.ticketcategory[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.ticketCategoryForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.ticketcatId == 0){ 
				this.ticketcategory['requestType'] = 'add';
			}else{
				this.ticketcategory['requestType'] = 'update';
				this.ticketcategory['ticketcatId'] = this.ticketcatId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/helpdeskcategory',
									JSON.stringify(this.ticketcategory),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.ticketCategoryForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.ticketcategory);
	  }
 
  onDelete(){
		 this.ticketcategory['requestType'] = 'delete';
		this.ticketcategory['ticketcatId'] = this.ticketcatId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/helpdeskcategory',
									JSON.stringify(this.ticketcategory),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.ticketCategoryForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  TicketCategoryData {
    ticketcatId: string;
    ticketcatName: string;
	ticketcatEntryId: string;
	ticketcatEntryDate: string;
	ticketcatModifiedId: string;
	ticketcatModifiedDate: string;
  
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



